<template>
  <div>
    <el-form direction="horizontal" label-width="120px" class="margin-top-20">
      <el-form-item label="">
          <span class="ant-form-text" id="userName" name="userName" style="font-size: 18px;">ACI</span>
      </el-form-item>
      <el-form-item label="" >
          <div class="inline-block" style="font-size: 28px;margin-right: 20px;">
          <span class="color-warn">{{price}}</span></div>
          <!-- <el-button type="default" html-type="button" @click="refreshPrice()"></el-button> -->
      </el-form-item>
      <el-form-item label="">
        <el-radio-group v-model="payType" @change="paySingup">
            <el-radio label="wechat"></el-radio>
            <el-radio label="alipay"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item v-if='payType=="alipay"' label="">
          
          <el-button type="primary" @click='jumpAliPage()'></el-button>
      </el-form-item>
      <el-form-item v-else label="" >
          <div style='width:200px;height:200px;padding:10px;border:1px solid #eee;'>
              <img :src='qrcode' style='width:100%;' />
          </div>
          <!-- <span><a class="link" href='http://news.chaozhiedu.com/wj/jinpailubo.docx'></a></span> -->
      </el-form-item>
      <!-- <el-form-item label="">
          <img src="/static/images/20180316111851.png">
      </el-form-item> -->
      <el-form-item label="">
          ACI<a></a>
      </el-form-item>
    </el-form>
    <div v-html='alipayForm'></div>
  </div>
</template>

<script>
let timer = 0;
import QRCode from "qrcode";
export default {
  name: "paySiginUp",
  data() {
    return {
      qrcode: "",
      price: "",
      payType: "wechat",
      alipayForm: ""
    };
  },
  methods: {
    paySingup() {
      //
      this.$czapi
        .toPaySingUp({
          channel: this.payType
        })
        .then(({ code, order_info, msg, form, pay_order_id, qrtext }) => {
          //console.log(code, msg);
          if (this.payType === "alipay") {
            this.alipayForm = form;
          } else {
            QRCode.toDataURL(qrtext, { errorCorrectionLevel: "H" }).then(
              url => {
                this.qrcode = url;
              }
            );
          }
          this.price = (order_info.price / 100).toFixed(2);
        });
    },
    getPayInfo() {
      //
      this.$czapi.getUserSignPayinfo({}).then(({ code, status }) => {
        if (status == 1) {
          window.location.reload();
        }
      });
    },
    jumpAliPage() {
      //console.log(document.getElementById("alipaysubmit"));
      document.getElementById("alipaysubmit").target = "_blank";
      document.forms["alipaysubmit"].submit();
    }
  },
  mounted() {
    this.paySingup();
    timer = setInterval(this.getPayInfo, 6180);
  },
  destroyed() {
    console.log("destroyed");
    clearTimeout(timer);
  }
};
</script>

<style>
</style>



