<script>
/**
 * @param file input
 * @param arrow 
 * @returns {boolean}
 * */
function verifyFileSuffix(file, arrow) {
  var arrowFile = arrow || "jpg,jpeg,png,gif,bmp,JPG,JPEG,PNG,GIF,BMP";
  var _path = file.value;
  var _fileArr = _path.replace(/(\\+)/g, "#").split("#");
  var _fileNameArr = _fileArr[_fileArr.length - 1].split(".");
  var _suffix = _fileNameArr[_fileNameArr.length - 1];
  var _result = arrowFile.indexOf(_suffix);
  if (_result >= 0) {
    return true;
  } else {
    showAlert("", 1070);
    return false;
  }
}

import paySiginUp from "@/components/paySiginUp";
import { mapState } from "vuex";
import {
    getToken
} from '@/utils/auth'
export default {
  name: "Info",
  components: {
    paySiginUp
  },
  data() {
    //console.log(this.$czapi);
    return {
      steps: 0,
      param: {
        cn_name: "", //
        en_name: "", //
        sex: "", // F  M 
        birthday: "", //
        college: "", //
        edu_num: "", //
        idcard: "", //,
        email: "",
        contacts_phone: "", //
        addr: "", //
        period: "", //ID
        idcard_front: "", // ID
        idcard_reverse: "", // ID
        edu: "", //ID
        degree: "", //ID
        entry_form: "", //ID
        avatar: "", //ID 
        idcard_front_file: "", //
        idcard_reverse_file: "", //
        edu_file: "", //
        degree_file: "", //
        entry_form_file: "", //
        avatar_file: "" //
      },
      info: {
        user: {},
        ext_info: {},
        period: []
      },

      sexType: [
        {
          value: "M",
          label: "M"
        },
        {
          value: "F",
          label: "F"
        }
      ],
      ksType: [],
      fileName: "file",
      uploadAction: this.$czapi.uploadAction,
      //cateList: [],
      // uploads: {
      //   idcard_front_file: "", //
      //   idcard_reverse_file: "", //
      //   edu_file: "", //
      //   degree_file: "", //
      //   entry_form_file: "", //
      //   avatar_file: "" //
      // }
      //fileList: [],
      pay: {
        show: false
      }
    };
  },
  // computed: {
  //   ...mapState({
  //     info: state => state.userInfo
  //     //cateList: state => state.cateList
  //   })
  // },
  watch: {
    info: {
      handler(val, old) {
        this.param = { ...this.param, ...val.ext_info, ...val.user };
        this.ksType = val.period;
        this.status = val.status;
        this.steps = [0, 1, 2, 4][val.status];
      },
      immediate: true
    }
  },
  methods: {
    disabledDate(current) {
      return current && current.valueOf() >= Date.now();
    },

    // onChangeIdCardZm(info) {
    //   if (info.file.status !== "uploading") {
    //     console.log(info.file, info.fileList);
    //   }
    //   if (info.file.status === "done") {
    //     console.log(info.file.name + " .");
    //   } else if (info.file.status === "error") {
    //     console.log(info.file.name + " .");
    //   }
    // },
    triggerUpload(type) {
      console.log(type);
      document.getElementById("wtFile").click();
    },
    showConfirmForm() {
      this.$confirm(
        "\n" + "",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: ""
        }
      )
        .then(() => {
          //console.log("");
          let params = {};

          for (const key in this.param) {
            if (this.param.hasOwnProperty(key)) {
              const element = this.param[key];
              if (element) {
                params[key] = element;
              }
            }
          }
          //
          this.$czapi
            .submitUserSign(params)
            .then(({ code, msg, data_msg = [] }) => {
              if (code != 200) {
                let keys = Object.keys.call(this, data_msg);
                if (keys.length > 0) {
                  this.$notify.error({
                    title: "",
                    message: data_msg[keys[0]]
                  });
                } else {
                  this.$message.error(msg);
                }
                return false;
              }

              this.$message({ message: msg, type: "success" });

              this.steps++;
            });
        })
        .catch(() => {});
    },

    //
    doSave() {
      let params = {};

      for (const key in this.param) {
        if (this.param.hasOwnProperty(key)) {
          const element = this.param[key];
          if (element) {
            params[key] = element;
          }
        }
      }

      //
      this.$czapi.saveUserSign(params).then(({ code, msg, data_msg = [] }) => {
        if (code != 200) {
          let keys = Object.keys.call(this, data_msg);
          //console.log(this);

          if (keys.length > 0) {
            this.$notify.error({
              title: "",
              message: data_msg[keys[0]]
            });
          } else {
            this.$message.error(msg);
          }
          return false;
        }

        this.$message.success(msg);
      });
    },
    onBeforeUpload(file, filename) {

      let formData = new FormData();

      formData.append("file", file);

      fetch(`${this.uploadAction}?token=${this.Token}`, {
        method: "POST",
        //headers,
        body: formData
      })
        .then(res => {
          return res.json();
        })
        .then(res => {
          //console.log(data, file, reqOptions, 111);
          if (res.code == 200) {
            //console.log(reqOptions, this.$refs);
            this.param[filename] = res.data.url;
            this.param[filename.replace("_file", "")] = res.data.id;
            //console.log(this.param);
            this.$message.success("");
          }
        });

      //this.fileList = [];

      return false;
    },
    getUserSign() {
      this.$czapi.getUserSign().then(res => {
        if (res.status == 3) {
          this.getUserSignPayinfo();
        }

        this.info = res;
      });
    },
    getUserSignPayinfo() {
      this.$czapi.getUserSignPayinfo().then(res => {
        //this.info = res;
        this.pay.show = res.status == 0;
      });
    }
  },
  mounted() {
    this.getUserSign();
    //   this.param = { ...this.param, ...this.info.ext_info, ...this.info.user };
    //   this.ksType = this.info.period;
  }
};
</script>

